import styled from 'styled-components'
import { cores } from '../../color/cores';

export const Box = styled.div`
  max-width: 800px;
  background: transparent;
  border-radius: 50px;
  margin: 10px 50px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

export const CaixaTitulo = styled.div`
  max-width: 700px;
  margin-bottom: 30px;
`;

export const Texto = styled.div`
  font-size: 2rem;
  color: #fff;
  font-weight: bold;
`;

export const BoxImg = styled.div`
  max-width: 700px;
  width: 700px;
`;

export const Img = styled.img`
  border-radius: 500px;
  width: 100%;
  box-shadow: 0 0 1rem black;
  transition: all ease-in-out 0.5s;
  transform: scale(0.9);

  &:hover{
    transform: scale(1);
    transition: all ease-in-out 0.5s;
  }
`;

export const CaixaBtn = styled.div`
  max-width: 300px;
  justify-content: center;
  align-items: center;
  display: flex;
`;

export const Reiniciar = styled.div`
  cursor: pointer;
  background-color: #fff;
  font-weight: bold;
  border-radius: 50px;
  padding: 10px 20px;
  border: 3px solid ${cores.vermelho};
  color: black;
  font-size: 1.5rem;
  box-shadow: 0 0 0.3rem black;
  transition: all ease-in-out 0.3s;

  &:hover{
    background-color: ${cores.vermelho};
    color: #fff;
    transition: all ease-in-out 0.3s;
  }
`;