import React from 'react';
import { Texto, Box, BoxImg, CaixaTitulo, Img, CaixaBtn, Reiniciar } from './ElementsFinal';
import carinhaMorto from '../../img/morto.svg'

const Final = ({reiniciar}) => {
  return (
    <Box>
      <CaixaTitulo>
        <Texto>Game Over</Texto>
      </CaixaTitulo>
      <CaixaBtn onClick={reiniciar}>
        <Reiniciar><p>Reiniciar</p></Reiniciar>
      </CaixaBtn>
      <BoxImg >
        <Img src={carinhaMorto}/>
      </BoxImg>
    </Box>
  );
}

export default Final;