import styled from 'styled-components';
import { cores } from '../../color/cores';

export const Container = styled.section`
  display: flex;
  flex-direction: column;
`;

export const CaixaTitulo = styled.div`
  max-width: 700px;
`;

export const CaixaInterface = styled.div`
  display: flex;
  flex-direction: column;
  margin: 30px;
`;

export const Nome = styled.h1`
  color: #010101;
  letter-spacing: 2px;
  margin-bottom: 20px;
  font-size: 3rem;
`;

export const CaixaVida = styled.div`
  display: flex;
  align-items: center;
`;

export const SubTexto = styled.p`
  font-size: 2rem;
  font-weight: bold;
  color: #010101;
  width: 50%;
`;

export const Vida = styled.h2`
  color: #FFF;
  background-color: ${cores.vermelhoEscuro};
  border-radius: 30px;
  padding: 5px 20px;
  margin-left: 10px;
  width: 50%;
  text-align: center;
  box-shadow: 0 0 0.1rem black  ;
  outline: red;
`;

export const Alimentos = styled.div`
  max-width: 300px;
  margin: 10px 30px;
  padding-right: 20px;
  border-radius: 50px;
  @media screen and (max-width:768px){
    margin: 0 30px;
  }
`;

export const Texto = styled.h1`
color: #010101;
font-size: 2rem;
margin-bottom: 10px;
`;

export const CaixaItems = styled.div`

`;
export const CaixaItem = styled.div`
  width: 25%;
`;

export const Item = styled.div`
border-radius: 200px;
max-width: 65px;
cursor: pointer;
margin-bottom: 10px;
background-size: contain;
padding-left:0px;
padding-top: 10px;
transform: scale(0.9);
transition: all ease-in-out 0.3s;
display: ${({view}) => (view ? 'block' : 'none')};

:hover{
  transform: scale(1.4);
  transition: all ease-in-out 0.3;
}
`;