import React from 'react';
import { Texto, Box, BoxImg, CaixaTitulo, Img, CaixaBtn, Reiniciar } from './Elements';
import carinhaMorto from '../../img/personagem.svg';

const Ganhou = ({reiniciar}) => {
  return (
    <Box>
      <CaixaTitulo>
        <Texto>Parabens! Você ganhou</Texto>
      </CaixaTitulo>
      <CaixaBtn onClick={reiniciar}>
        <Reiniciar><p>Reiniciar</p></Reiniciar>
      </CaixaBtn>
      <BoxImg >
        <Img src={carinhaMorto}/>
      </BoxImg>
    </Box>
  );
}

export default Ganhou;