import React, { useEffect, useState } from 'react';
import { Box, BoxImg, Img } from './PersonagemElements';
import carinhaFeliz from '../../img/personagem.svg';
import carinhaTriste from '../../img/triste.svg';
import carinhaNausea from '../../img/nausea.svg'

const Personagem = ({vida}) => {
  const [emocao, setEmocao] = useState(0);
  const imagem = [
    <Img src={carinhaFeliz} />,
    <Img src={carinhaTriste} />,
    <Img src={carinhaNausea} />,
  ]
  useEffect(() => {
    if (vida <= 90) {
      setEmocao(1)
    }
    if (vida <= 50) {
      setEmocao(2)
    }
  }, [vida])
  return (
    <Box>
      <BoxImg>
        {imagem[emocao]}
      </BoxImg>
    </Box>
  );
}

export default Personagem;