import styled from 'styled-components';
import pizza from '../img/pizza.svg';
import hamburguer from '../img/hamburguer.svg';
import cigarro from '../img/cigarro.svg';
import alface from '../img/alface.png';
import cerveja from '../img/cerveja.png';
import maca from '../img/maçã.png';
import cabeceira from '../img/cabeceira.jfif';
import legumes from '../img/legumes.png';

const Icone = styled.img`
  width: 100%;
  
`;

export const icones = {
  pizza: <Icone src={pizza}/>,
  alface: <Icone src={alface}/>,
  hamburguer: <Icone src={hamburguer}/>,
  cigarro: <Icone src={cigarro} />,
  cerveja: <Icone src={cerveja}/>,
  maça: <Icone src={maca} />,
  cabeceira: <Icone src={cabeceira} />,
  legumes: <Icone src={legumes}/>,
}