import React from 'react';
import { icones } from '../../data/data';
import { Alimentos, Texto, Item, CaixaTitulo, Nome, Vida, SubTexto, CaixaVida, CaixaInterface, Container, CaixaItems } from './ElementsItens';

const Items = ({ fumar, comerPizza, comerHamburguer, comerAlface, beberCerveja, displayF, displayH, displayP, displayA, displayC, vida, displayMaca, comerMaca, displayCabeceira, cabeceira }) => {

  return (
    <Container>
      <CaixaTitulo>
        <CaixaInterface>
          <Nome>Paciente</Nome>
          <CaixaVida>
            <SubTexto>Heath:</SubTexto>
            <Vida>{vida}%</Vida>
          </CaixaVida>
        </CaixaInterface>
      </CaixaTitulo>
      <Alimentos>
        <Texto>Hábitos</Texto>
        <CaixaItems>
          <Item onClick={comerPizza} view={displayP}>{icones.pizza}</Item>
          <Item onClick={comerMaca} view={displayMaca}>{icones.maça}</Item>
          <Item onClick={comerHamburguer} view={displayH}>{icones.hamburguer}</Item>
          <Item onClick={cabeceira} view={displayCabeceira}>{icones.cabeceira}</Item>
          <Item onClick={fumar} view={displayF}>{icones.cigarro}</Item>
          <Item onClick={beberCerveja} view={displayC}>{icones.cerveja}</Item>
          <Item onClick={comerAlface} view={displayA}>{icones.legumes}</Item>
        </CaixaItems>
      </Alimentos>
    </Container>
  );
}

export default Items;