import styled from 'styled-components';
import { cores } from '../../color/cores';

export const BoxContainer = styled.div`
  max-width: 1100px;
  margin: 0 auto;
`;

export const CaixaConteudo = styled.div`
  border: 3px solid ${cores.vermelho};
  box-shadow: 0 0 1em black;
  max-width: 550px;
  background: #fff;
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 10px;
`;


export const CaixaTexto = styled.div`
  max-width: 550px;
`;

export const Texto = styled.h1`
  font-size: 2rem;
  margin: 20px;
  color: #010101;
  text-align: center;
`;

export const BoxButton = styled.div`
  max-width: 200px;
  width: 200px;
`;

export const Button = styled.div`
  border: 3px solid ${cores.vermelho};
  border-radius: 50px;
  padding: 14px 40px;
  background: #fff;
  transition: all ease-in-out 0.3s;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  display: flex;
  
  font-size: 1.5rem;

  :hover{
    background: ${cores.vermelho};
    transition: all ease-in-out 0.3s;
    color:#fff;
    box-shadow: 0 0 0.5em black;
  }
`;