import React from 'react';
import { BoxButton, BoxContainer, Button, CaixaConteudo, CaixaTexto, Texto } from './HomeElements';

const Home = ({proximo}) => {
  return (
    <BoxContainer>
      <CaixaConteudo>
        <CaixaTexto>
          <Texto>
            Escolha os melhores habitos para ganhar.
          </Texto>
        </CaixaTexto>
        <BoxButton>
          <Button onClick={proximo}>Start</Button>
        </BoxButton>
      </CaixaConteudo>
    </BoxContainer>
  );
}

export default Home;