import React from 'react';
import Items from '../Itens/Items';
import Personagem from '../Personagem';
import { Box, ContainerGame } from './GameElements';

const Game = ({ vida, fumar, comerPizza, comerHamburguer, comerAlface, beberCerveja, displayF, displayH, displayP, displayA, displayC, pontos, displayMaca, comerMaca, displayCabeceira, cabeceira }) => {
  return (
    <ContainerGame>
      <Box>
        <Items
          fumar={fumar}
          comerPizza={comerPizza}
          comerHamburguer={comerHamburguer}
          comerAlface={comerAlface}
          comerMaca={comerMaca}
          beberCerveja={beberCerveja}
          displayF={displayF}
          displayH={displayH}
          displayP={displayP}
          displayA={displayA}
          displayC={displayC}
          displayMaca={displayMaca}
          displayCabeceira={displayCabeceira}
          cabeceira={cabeceira}
          vida={vida}
          pontos={pontos}
        />
        <Personagem vida={vida} />
      </Box>
    </ContainerGame>
  );
}

export default Game;