import { useState, useEffect } from 'react';
import './App.css';
import Home from './components/Inicio';
import Game from './components/Game';
import Final from './components/Final';
import { danoCigarro, danoPizza, danoHamburguer, danoCerveja} from './models/regras';
import fundo from './img/fundo3.png';
import { Background, Fundo } from './ElementsApp';
import Ganhou from './components/Ganhou';

function App() {
  const [etapa, setEtapa] = useState(0);
  const [vida, setVida] = useState(100);
  const [pontos, setPontos] = useState(0);
  const [displayF, setDisplayF] = useState(true);
  const [displayP, setDisplayP] = useState(true);
  const [displayH, setDisplayH] = useState(true);
  const [displayA, setDisplayA] = useState(true);
  const [displayC, setDisplayC] = useState(true);
  const [displayMaca, setDisplayMaca] = useState(true);
  const [displayCabeceira, setDisplayCabeceira] = useState(true)

  function comerAlface() {
    setPontos(pontos + 10)
    setDisplayA(false);
    console.log(pontos);
  }

  function cabeceira() {
    setPontos(pontos + 10)
    setDisplayCabeceira(false);
    console.log(pontos);
  }

  function comerMaca() {
    setPontos(pontos + 10)
    setDisplayMaca(false);
    console.log(pontos);
  }

  useEffect(() => {
    if (etapa > (pagina.length - 1)) {
      proximo();
    }
  })

  useEffect(() => {
    if(vida <= 0){
      setEtapa(2)
    }
  },[vida, etapa])

  useEffect(() => {
    if(pontos >= 30){
      setEtapa(3)
    }
  },[pontos, etapa])

  function proximo() {
    setEtapa(etapa + 1)
  }

  function fumar() {
    const fumo = danoCigarro(vida);
    setVida(fumo);
    setDisplayF(false);
  }

  function comerPizza() {
    const comer = danoPizza(vida);
    setVida(comer);
    setDisplayP(false);
  }

  function comerHamburguer() {
    const comer = danoHamburguer(vida);
    setVida(comer);
    setDisplayH(false);
  }
  function beberCerveja() {
    const comer = danoCerveja(vida);
    setVida(comer);
    setDisplayC(false);
  }

  function reiniciar(){
    setEtapa(1);
    setVida(100);
    setDisplayH(true);
    setDisplayP(true);
    setDisplayA(true);
    setDisplayC(true);
    setDisplayF(true);
    setDisplayMaca(true);
    setPontos(0);
    setDisplayCabeceira(true);
  }

  const pagina = [
    <Home proximo={proximo} />,
    <Game
      vida={vida}
      fumar={fumar}
      comerPizza={comerPizza}
      comerHamburguer={comerHamburguer}
      comerAlface={comerAlface}
      comerMaca={comerMaca}
      beberCerveja={beberCerveja}
      displayF={displayF}
      displayH={displayH}
      displayP={displayP}
      displayA={displayA}
      displayC={displayC}
      displayMaca={displayMaca}
      displayCabeceira={displayCabeceira}
      cabeceira={cabeceira}
    />,
    <Final reiniciar={reiniciar}/>,
    <Ganhou reiniciar={reiniciar}/>
  ]
  return (
    <Background>
      <Fundo src={fundo} />
      {pagina[etapa]}
    </Background>
  );
}

export default App;
